.nav {
    position: fixed;
    width: 100%;
    transition: 200ms linear;
}

.nav-black {
    position: fixed;
    width: 100%;
    transition: 200ms linear;
    background: rgb(27, 27, 27);
    z-index: 1;
}

.navbar {
    margin-top: auto;
    display: flex;
    justify-content: center;
}

.navbar li {
    padding-top: 1%;
    list-style: none;
    margin-left: 3%;
    margin-right: 3%;
    display: inline;
}

.navbar a {
    color:white;
    text-decoration: none;
    font-size: 150%;
    transition: 100ms linear;
}

.navbar a:hover {
    font-size: 170%;
    transition: 100ms linear;
}

@media screen and (min-width: 601px) {
    .hamburger-menu-close, .hamburger-menu-open {
        display:none;
    }
}

@media screen and (max-width: 600px) {
    .nav {
        display: none;
    }

    .nav-black {
        display: none;
    }

    .hamburger-open, .hamburger-close {
        display: fixed;
        position: fixed;
        padding-left: 3%;
        padding-top: 2%;
        padding-bottom: 2%;
        position: fixed;
        width: 100%;
        background: rgb(27, 27, 27);
        z-index: 10;
    }

    .bar1, .bar2, .bar3 {
        width: 35px;
        height: 5px;
        background-color: rgb(202, 202, 202);
        margin: 6px 0;
        transition: 0.4s;
    }
    
    .hamburger-open .bar1 {
        -webkit-transform: rotate(-45deg) translate(-9px, 6px);
        transform: rotate(-45deg) translate(-9px, 6px);
    }
      
    .hamburger-open .bar2 {
        opacity: 0;
    }
      
    .hamburger-open .bar3 {
        -webkit-transform: rotate(45deg) translate(-8px, -8px);
        transform: rotate(45deg) translate(-8px, -8px);
    }

    .hamburger-menu-close {
        display: none;
    }

    .hamburger-menu-open { 
        display: fixed;
        background: rgb(27, 27, 27);
    }
    
    .hamburger-menu-open a {
        text-decoration: none;
        font-size: 150%;
        color: rgb(202, 202, 202);
    }

    .hamburger-menu-open li {
        list-style: none;
        padding-bottom: 2%;
    }
}
